/* src/components/About.css */
.about-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.about-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.about-container p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  color: #666;
}

.about-container h2 {
  margin-top: 2rem;
  font-size: 1.8rem;
  color: #333;
}

.about-container a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.about-container a:hover {
  text-decoration: underline;
}

