/* src/components/Navbar.css */
.navbar {
  background-color: #282c34;
  padding: 1rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.navbar-logo {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.navbar-links {
  display: flex;
  align-items: center;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  margin: 0 1rem;
}

.navbar-links a:hover {
  text-decoration: underline;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .navbar-links {
    display: none;
  }

  .navbar-logo {
    font-size: 1.2rem;
  }

  .navbar-menu-icon {
    display: block;
    cursor: pointer;
    color: white;
  }
}

@media (min-width: 769px) {
  .navbar-menu-icon {
    display: none;
  }
}

