/* src/components/Projects.css */

.content {
  padding-bottom: 4rem; /* Ensure space for the fixed footer */
}

.projects-container {
  padding: 2rem;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.project-card {
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 0.25rem;
  padding: 1rem;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.project-card:hover {
  transform: translateY(-10px);
}

.project-card h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.project-card p {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.project-card a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.project-card a:hover {
  text-decoration: underline;
}

