/* src/components/Contact.css */
.contact-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.contact-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.contact-container form {
  display: flex;
  flex-direction: column;
}

.contact-container label {
  margin-bottom: 1rem;
  color: #666;
}

.contact-container input,
.contact-container textarea {
  width: 100%;
  padding: 0.5rem;
  margin-top: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contact-container button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #61dafb;
  color: white;
  cursor: pointer;
  margin-top: 1rem;
}

.contact-container button:hover {
  background-color: #21a1f1;
}

.form-status {
  margin-top: 1rem;
  color: #d9534f;
  font-size: 0.9rem;
}

/* Styles for the switch option in registration */
.switch-container {
  margin-top: 1rem;
}

.switch-container p {
  margin-bottom: 0.5rem;
  color: #d9534f;
  font-size: 0.9rem;
}

.switch-container button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #61dafb;
  color: white;
  cursor: pointer;
  margin-right: 0.5rem;
}

.switch-container button:hover {
  background-color: #21a1f1;
}

