/* src/components/Register.css */
.register-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.register-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.register-container form {
  display: flex;
  flex-direction: column;
}

.register-container label {
  margin-bottom: 1rem;
  color: #666;
}

.register-container input {
  width: 100%;
  padding: 0.5rem;
  margin-top: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.register-container button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #61dafb;
  color: white;
  cursor: pointer;
  margin-top: 1rem;
}

.register-container button:hover {
  background-color: #21a1f1;
}

.form-status {
  margin-top: 1rem;
  color: #d9534f;
  font-size: 0.9rem;
}

