/* src/App.css */

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap {
  flex: 1;
  padding-bottom: 2rem; /* Ensure space for footer */
}

.footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 1rem 0;
  position: relative; /* Changed from fixed to relative */
  width: 100%;
  font-size: 0.9rem; /* Reduced font size */
}

.footer a {
  color: #61dafb;
  text-decoration: none;
  margin: 0 0.5rem;
}

.footer a:hover {
  text-decoration: underline;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-links {
  margin-top: 0.5rem;
}

.footer-links a {
  color: #61dafb;
  margin: 0 0.5rem;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}

.navbar {
  background-color: #282c34;
  padding: 1rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

