/* src/components/Home.css */
.home-container {
  padding: 2rem;
  text-align: center;
}

.home-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.home-container p {
  font-size: 1.2rem;
  line-height: 1.5;
  margin: 1rem 0;
}

.home-links {
  margin-top: 2rem;
}

.home-link {
  display: inline-block;
  margin: 0.5rem 1rem;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  color: #fff;
  background-color: #333;
  text-decoration: none;
  border-radius: 5px;
}

.home-link:hover {
  background-color: #555;
}

