/* src/components/Profile.css */

.profile-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.profile-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.profile-container p {
  font-size: 1.2rem;
  color: #666;
}

.profile-info {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.profile-info label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.profile-info div {
  margin-bottom: 1rem;
}

.profile-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

.profile-buttons button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #61dafb;
  color: white;
  cursor: pointer;
  margin-top: 1rem;
}

.profile-buttons button:hover {
  background-color: #21a1f1;
}

.alert-message {
  margin-top: 1rem;
  color: #d9534f;
}

